/*Event
*/

.EventImg {
    width: 100%;
    height: 100%;
    border-radius: 16px;
}

.BlocImg {
    width: 200px;
    height: 150px;
    margin-right: 1px;
}
.Chevron{
    width: 24px;
    height: 24px;
    margin-top: 80px;
    margin-left: 20px;
    cursor: pointer;
    opacity: 0.4;
    -webkit-transition: all 0.8 ease;
    -o-transition: all 0.8 ease;
    transition: all 0.8 ease;  
}
.Chevron:hover{
    -webkit-transform: scale(2);
        -ms-transform: scale(2);
            transform: scale(2);
    opacity: 1;
    -webkit-transition: all 0.8 ease;
    -o-transition: all 0.8 ease;
    transition: all 0.8 ease;
}
.EventTitre {
    color: #D72F2F;
    margin-bottom: -10px;
    font-size: 16px;
    text-align: center;
}

.EventDate {
    text-align: end;
    font-weight: bold;
}

.EventDescription {
    text-align: justify;
    width: 300px;
    margin-top: -10px;

}

.EventHeader {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;

}

.Event {
    cursor: pointer;
    padding-top: 10px;
    margin: 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    border-radius: 6px;
    width: 100%;
}

.Event:hover {
    -webkit-box-shadow: 1px 1px 1px 1px #ccc;
            box-shadow: 1px 1px 1px 1px #ccc;
}

.BlocHeaderEvent{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    width: 95%;
        
}
.BlocChevron {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}
/*Evenements*/
.EvenementEvents {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 100%;
}

.Evenement {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    overflow: hidden;
}


