


/*HeadeActu*/
 
.Chevron{
    width: 24px;
    height: 24px;
    margin-top: 80px;
    margin-left: 20px;
    cursor: pointer;
    opacity: 0.4;
    -webkit-transition: all 0.8 ease;
    -o-transition: all 0.8 ease;
    transition: all 0.8 ease;  
}
.Chevron:hover{
    -webkit-transform: scale(2);
        -ms-transform: scale(2);
            transform: scale(2);
    opacity: 1;
    -webkit-transition: all 0.8 ease;
    -o-transition: all 0.8 ease;
    transition: all 0.8 ease;
}
.BlocHeaderActu{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    width: 100%;
        
}
.Ac{
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-right: 10px;
    overflow: hidden;
}





